import SendsayApi from 'sendsay-api';

class Sendsay {
  sendsayApi = new SendsayApi();

  authorization = async ({ login, password, sublogin }) => {
    const options = {
      login,
      password,
    };

    if (sublogin) {
      sessionStorage.setItem('isLoggedWithSublogin', true);
      options.sublogin = sublogin;
    } else {
      sessionStorage.setItem('isLoggedWithSublogin', false);
    }

    try {
      await this.sendsayApi.login(options);
      this.saveSession();

      const user = await this.getUser();
      return user;
    } catch (err) {
      throw this.handleError(err);
    }
  };

  saveSession = () => {
    const { session } = this.sendsayApi;
    sessionStorage.setItem('sendsay_session', session);
  };

  authFromSession = async () => {
    this.setSession();

    const user = await this.getUser();
    return user;
  };

  setSession = () => {
    const session = sessionStorage.getItem('sendsay_session');
    this.sendsayApi.setSession(session);
  };

  getUser = async () => {
    const options = {
      action: 'pong',
    };

    try {
      const { account, sublogin } = await this.sendsayApi.request(options);

      const user = {
        login: account,
      };

      if (this.isLoggedWithSublogin()) {
        user.sublogin = sublogin;
      }

      return user;
    } catch (err) {
      throw this.handleError(err);
    }
  };

  isLoggedWithSublogin = () => {
    const json = sessionStorage.getItem('isLoggedWithSublogin');
    return JSON.parse(json);
  };

  sendRequest = async options => {
    try {
      return await this.sendsayApi.request(options);
    } catch (err) {
      throw this.handleError(err);
    }
  };

  logout = async () => {
    const options = {
      action: 'logout',
    };

    try {
      await this.sendsayApi.request(options);
    } catch (err) {
      throw this.handleError(err);
    }
  };

  handleError = err => {
    if (err.id && err.explain) {
      const str = `{id: "${err.id}", explain: "${err.explain}"}`;
      return str;
    }

    if (err.message && err.message.includes('fetch')) {
      return err.message;
    }

    return err;
  };
}

export default new Sendsay();
