/* eslint-disable no-useless-escape */

import { isContainCyrillic, isEmail, isStringEmpty } from '../validation';

class SendsayValidation {
  forbiddenSymbols = /[!"#$%&'()\-*+\,.\/:;<=\]?@\[>\\^`{|}~]/;

  isLoginCorrect = login => {
    if (isStringEmpty(login) || isContainCyrillic(login)) {
      return false;
    }

    if (isEmail(login)) {
      return true;
    }

    if (login.match(this.forbiddenSymbols)) {
      return false;
    }

    return true;
  };

  isSubloginCorrect = sublogin => {
    if (isContainCyrillic(sublogin) || sublogin.match(this.forbiddenSymbols)) {
      return false;
    }

    return true;
  };

  isPasswordCorrect = password => {
    if (isStringEmpty(password) || isContainCyrillic(password)) {
      return false;
    }

    return true;
  };
}

export default new SendsayValidation();
